$red: #EB5757;

.modal {
    position: relative;
    width: 469px;
    min-height: 30px;

    background: #FFFFFF;
    box-shadow: 10px 27px 42px rgba(168, 168, 168, 0.13);
    border-radius: 12px;
    padding: 31px 0 53px;

    &-wrapper {
        height: 100vh;
        width: 100vw;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 99999;
        background: rgba(0, 0, 0, 0.5);
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &_close {
        position: absolute;
        top: 10px;
        right: 10px;
        width: 24px;
        height: 24px;
        border: 2px solid $red;
        border-radius: 5px;
        background: transparent;
        font-size: 22px;
        font-weight: bold;
        color: $red;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}