.notify {
    padding: 16px 25px;
    width: 100%;
    background: white;
    box-shadow: 1px 8px 25px rgba(168, 168, 168, 0.13);
    margin-bottom: 10px;
    border-radius: 5px;
    position: relative;
    z-index: 1;

    &_close {
        display: flex;
        justify-content: space-around;
        align-items: center;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 6px;
        cursor: pointer;
        font-size: 36px;
        width: 25px;
        height: 25px;
        z-index: 3;
    }

    &_text {
        width: 90%;
        text-overflow: ellipsis;
        overflow: hidden;
    }

    &__success {
        $bgColor: #28C76F;

        &:after {
            position: absolute;
            content: '';
            top: 0;
            right: 0;
            left: 0;
            bottom: 0;
            width: 100%;
            height: 100%;
            border: 0.5px solid $bgColor;
            background: rgba($bgColor, 0.11);
            z-index: 2;
        }
    }

    &__warning {
        $bgColor: rgb(233, 177, 72);

        &:after {
            position: absolute;
            content: '';
            top: 0;
            right: 0;
            left: 0;
            bottom: 0;
            width: 100%;
            height: 100%;
            border: 0.5px solid $bgColor;
            background: rgba($bgColor, 0.11);
            z-index: 2;
        }
    }

    &__error {
        $bgColor: red;

        &:after {
            position: absolute;
            content: '';
            top: 0;
            right: 0;
            left: 0;
            bottom: 0;
            width: 100%;
            height: 100%;
            border: 0.5px solid $bgColor;
            background: rgba($bgColor, 0.11);
            z-index: 2;
        }
    }
}