$purple: #5D5FEF;

.btn {
    border: none;
    background: transparent;
    color: $purple;
    font-size: 10px;
    line-height: 24px;
    cursor: pointer;
    display: flex;
    align-items: center;

    &:hover {
        color: darken($purple, 15);

        .btn_plus {
            border: 2px solid darken($purple, 15);
            color: darken($purple, 15);
        }
    }

    &_plus {
        display: inline-block;
        margin-right: 8px;
        height: 24px;
        width: 24px;
        border-radius: 50%;
        border: 2px solid $purple;
        font-size: 18px;
        line-height: 22px;
        font-weight: bold;
        color: $purple;
    }
}