.indicator {
    display: block;
    width: 50px;
    height: 50px;
    border: 2px solid lightblue;
    border-radius: 50%;
    position: relative;

    &-container {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &::after {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        border: 3px solid darken(lightblue, 25);
        border-radius: 50%;
        clip: rect(0, 25px, 25px, 0);
        transform: rotate(180deg);
        animation-duration: 1s;
        animation-name: slidein;
        animation-iteration-count: infinite;
    }
}

@keyframes slidein {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}