.select {
    border-radius: 6px;
    font-weight: bold;
    font-size: 14px;
    line-height: 24px;
    color: #5D5FEF;
    user-select: none;
    position: relative;
    height: 28px;

    &_svg {
        width: 24px;
        height: 24px;
        display: block;
        text-align: center;

        &.open {
            transform: rotate(180deg);
        }
    }

    &_option,
    &_label {
        padding: 2px 8px;
        cursor: pointer;
    }

    &_label {
        background: #F8F7FC;
        display: flex;
        justify-content: space-between;
    }

    &_options {
        position: absolute;
        text-align: center;
        top: 28px;
        left: 0;
        right: 0;
        background: white;
        z-index: 999;
    }

    &_option {
        &:hover {
            background: darken(white, 5);
        }
    }
}