.button {
    width: 100%;
    background: #7367F0;
    border-radius: 5px;
    height: 38px;
    border: none;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.4px;
    color: #FFFFFF;
    cursor: pointer;

    &:hover {
        background: darken(#7367F0, 5);
    }
}