@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Montserrat';
}

body {
    color: #6E6B7B;
}

/* полоса прокрутки (скроллбар) */
::-webkit-scrollbar {
    width: 13px;
    height: 13px;
    background: #EBE9F1;
}

/* ползунок скроллбара */
::-webkit-scrollbar-thumb {
    background: #5D5FEF;
    border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
    background: darken(#5D5FEF, 10);
}

/* Стрелки */

::-webkit-scrollbar-button {
    height: 0;
}

.container {
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(155.04deg, #FFFFFF 38.79%, #F4F3F9 100%);
}

h2 {
    font-weight: bold;
    font-size: 34px;
    line-height: 44px;
    margin-bottom: 32px;
}

h3 {
    font-weight: bold;
    font-size: 18px;
    line-height: 24px;
}

table {
    border-spacing: 0;
}

a {
    text-decoration: none;
}